@import url("https://fonts.googleapis.com/css2?family=Monsterrat&display=swap");

html {
  font-size: 60.5%;
  box-sizing: border-box;
}
body,
#root{
  height: 100vh;
  margin: 0;
}

body, input, select, textarea,button {
  font-family: Monsterrat, Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
 
}

.grid-container{
  display: grid;
  grid-template-areas: "header"
  "main"
  "footer";
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}

header{
  grid-area: header;
  background-color:  #ffffff;
  color: #8d959c;
  display: flex;
  align-items: center;
  padding: 0.5rem;
  margin-bottom: 55rem;

}

header a{
  color: #203040;
}

a {
  text-decoration: none;
}

a:hover{
  color: #2fcca5;
  text-decoration: none;
}

main{
  grid-area: main;
}
footer{
  grid-area: footer;
  background-color: #ffffff;
  color: #203040;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.content{
  display: flex;
  flex-wrap: wrap;
  }

.main{
  flex: 3 60rem;

}

.sidebar{
  flex: 1 20rem;
}

/*products*/
.products{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.products li{
  flex: 0 1 29rem;
  height: 47rem;
  padding: 1rem;

}

.product{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.product img{
  max-width: 37rem;
  max-height: 37rem;
}

.product-price{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-price div,
.product-price button{
  text-align: center;
  flex: 1;
}

.product-price div{
  font-size: 2rem;
}

.button{
  padding: 1rem;
  border: .1rem #c0c0c0 solid;
  background-color: #2fccb7d0;
  cursor: pointer;
  }

  .button:hover{
    border: .1rem #808080 solid;
  }

.button.primary{
  background-color: #2fccb7d0;
}

/* Filter */

.filter{
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  border-bottom: .45rem #2fccb7d0 solid;
}

.filter div{
  flex: 1;
}

/*Cart*/
.cart{
  padding: 1rem;
  margin: 1rem;
  display: flex;
  flex-wrap: wrap;
}
.cart-header{
  border-bottom: .45rem #2fccb7d0 solid;
}

.cart-items{
  padding: 0;
  width: 100%;
  list-style-type: none;
}

.cart-items img{
  width: 5rem;
}

.cart-items li{
  display: flex;
}

.cart-items li div{
  padding: .5rem;
}

.cart-items li div:last-child{
  flex: 1;
}

.right{
  text-align: right;
}

.total{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.total > div{
  font-size: 2rem;
}
.total > div, .total > button{
  flex: 1;
}